import React from "react";

interface HeaderLogoProps {
  title?: string;
  withLogo?: boolean;
}

export default function HeaderLogo({
  title,
  withLogo = true
}: HeaderLogoProps) {
  return (
    <div className="idsk-header__logo">
      <a className="idsk-header__link idsk-header__link--homepage">
        {withLogo && (
          <span className="idsk-header__logotype">
            <HeaderLogoSVG />
          </span>
        )}
        {title && <span className="idsk-header__product-name">{title}</span>}
      </a>
    </div>
  );
}

