import React from "react";
import { TabPanelProps } from "./TabPanel";

interface TabsProps {
  children:
    | React.ReactElement<TabPanelProps>
    | Array<React.ReactElement<TabPanelProps>>;
  onChange: (index: number) => void;
  tabs: Array<{ id: string; tab: string }>;
  title?: string;
  tabIndex: number;
}

export default function Tabs({
  tabIndex,
  children,
  onChange,
  tabs,
  title
}: TabsProps) {
  function onTabChange(newValue: number) {
    onChange(newValue);
  }
  return (
    <div className="govuk-tabs" data-module="govuk-tabs">
      {title && <h2 className="govuk-tabs__title">{title}</h2>}
      <ul className="govuk-tabs__list" role="tablist">
        {tabs.map((it, index) => (
          <li
            key={it.id}
            role="presentation"
            className={`govuk-tabs__list-item ${
              index === tabIndex ? "govuk-tabs__list-item--selected" : ""
            }`}
            onClick={() => onTabChange(index)}
          >
            <a
              role="tab"
              className="govuk-tabs__tab"
              href={`#${index}-tab`}
              tabIndex={index === tabIndex ? 0 : -1}
            >
              {it.tab}
            </a>
          </li>
        ))}
      </ul>
      {children}
    </div>
  );
}
