import React from "react";

interface HeaderNavigationProps {
  navigation?: Array<{ item: string; href: string; active: boolean }>;
  service?: string;
}

export default function HeaderNavigation({
  service,
  navigation
}: HeaderNavigationProps) {
  return (
    <div className="idsk-header__content">
      {service && (
        <a className="idsk-header__link idsk-header__link--service-name">
          {service}
        </a>
      )}
      {navigation && (
        <button
          type="button"
          className="idsk-header__menu-button govuk-js-header-toggle"
          aria-controls="navigation"
          aria-label="Show or hide Top Level Navigation"
        >
          Menu
        </button>
      )}
      {navigation && (
        <nav>
          <ul
            id="navigation"
            className="idsk-header__navigation "
            aria-label="Top Level Navigation"
          >
            {navigation.map((nav) => (
              <li
                key={nav.item}
                className={`idsk-header__navigation-item ${
                  nav.active ? "idsk-header__navigation-item--active" : ""
                }`}
              >
                <a className="idsk-header__link" href={nav.href}>
                  {nav.item}
                </a>
              </li>
            ))}
          </ul>
        </nav>
      )}
    </div>
  );
}
