import React from "react";

interface TableCellProps {
  isPrimary: boolean;
  value: any;
  align?: "left" | "right";
}

export default function TableCell({
  isPrimary,
  value,
  align = "left"
}: TableCellProps) {
  return isPrimary ? (
    <th
      scope="row"
      className={`govuk-table__header ${
        align === "right" ? "govuk-table__cell--numeric" : ""
      }`}
    >
      {value}
    </th>
  ) : (
    <td
      className={`govuk-table__cell ${
        align === "right" ? "govuk-table__cell--numeric" : ""
      }`}
    >
      {value}
    </td>
  );
}
