import React from "react";

interface HeaderUserProps {
  onLogout: () => void;
  user?: string;
}
export default function HeaderUser({ onLogout, user }: HeaderUserProps) {
  return (
    <div className="idsk-header__user govuk-grid-column-one-quarter idsk-header__user--end">
      <UserIcon />
      <div className="idsk--header__user-name">
        {user && <span>{user}</span>}
        <div>
          <a onClick={onLogout} href="#" className="idsk--header__user-logout">
            Odhlsi sa
          </a>
        </div>
      </div>
    </div>
  );
}

function UserIcon() {
  return (
    <svg className="idsk-header__user-icon" viewBox="0 0 28 28">
      <path
        d="M5 21.9C5 19.8 6.8 18 9 18h10c2.2 0 4 1.8 4 3.9 1.9-2.1 3-4.9 3-7.9 0-6.6-5.4-12-12-12S2 7.4 2 14c0 3 1.1 5.8 3 7.9zm9 6.1C6.3 28 0 21.7 0 14S6.3 0 14 0s14 6.3 14 14-6.3 14-14 14zm0-12c-2.8 0-5-2.2-5-5s2.2-5 5-5 5 2.2 5 5-2.2 5-5 5z"
        fillRule="evenodd"
        clipRule="evenodd"
      />
    </svg>
  );
}
