import React from "react";

type ButtonType = "submit" | "reset" | "button";

export interface ButtonProps {
  children: React.ReactNode;
  className?: string;
  disabled?: boolean;
  id?: string;
  onClick?: () => void;
  type?: ButtonType;
}

export default function Button({
  children,
  className,
  disabled = false,
  id,
  onClick,
  type = "submit"
}: ButtonProps) {
  return (
    <button
      aria-disabled={disabled}
      className={`idsk-button ${disabled ? "idsk-button--disabled" : ""} ${
        className || ""
      }`}
      data-module="idsk-button"
      disabled={disabled}
      id={id}
      onClick={onClick}
      type={type}
    >
      {children}
    </button>
  );
}
