import React from "react";
import InputProps from "./input";

interface TextInputProps extends InputProps {}

export default function TextInput({
  name,
  className,
  defaultValue,
  disabled,
  error,
  errorClassName,
  helperClassName,
  fullWidth,
  helperText,
  label,
  labelClassName,
  value,
  type = "text",
  onChange,
  width = 30
}: TextInputProps) {
  return (
    <div
      className={`govuk-form-group ${error ? "govuk-form-group--error" : ""}`}
    >
      {label && (
        <label className={`govuk-label ${labelClassName || ""}`} htmlFor={name}>
          {label}
        </label>
      )}
      {helperText && (
        <span
          id={`${name}-hint"`}
          className={`govuk-hint ${helperClassName || ""}`}
        >
          {helperText}
        </span>
      )}
      {error && (
        <span
          id={`${name}-error`}
          className={`govuk-error-message ${errorClassName || ""}`}
        >
          <span className="govuk-visually-hidden">Error:</span> {error}
        </span>
      )}
      <input
        aria-describedby={`${name}-hint`}
        className={`govuk-input ${error ? "govuk-input--error" : ""} ${
          !fullWidth && width ? `govuk-input--width-${width}` : ""
        } ${className || ""}`}
        disabled={disabled}
        id={name}
        name={name}
        value={value}
        defaultValue={defaultValue}
        type={type}
        onChange={onChange}
      />
    </div>
  );
}
