import React from "react";
import ErrorAlert from "./ErrorAlert";
import SuccessAlert from "./SuccessAlert";

export interface AlertProps {
  message: string;
  description?: string;
  type?: "error" | "success";
  visible?: boolean;
}

export default function Alert({
  message,
  type = "error",
  description,
  visible = false
}: AlertProps) {
  function renderAlert() {
    switch (type) {
      case "error":
        return <ErrorAlert message={message} description={description} />;
      case "success":
        return <SuccessAlert message={message} />;
      default:
        return <SuccessAlert message={message} />;
    }
  }
  return visible ? renderAlert() : null;
}
