import React from "react";

type GridWidthType =
  | "row"
  | "full"
  | "half"
  | "third"
  | "two-thirds"
  | "quarter";

export interface GridProps {
  children: React.ReactNode;
  className?: string;
  width?: GridWidthType;
}

export default function Grid({
  children,
  className,
  width = "full"
}: GridProps) {
  function renderGrid() {
    switch (width) {
      case "row":
        return (
          <div className={`govuk-grid-row ${className || ""}`}>{children}</div>
        );
      case "full":
        return (
          <div className={`govuk-grid-column-full ${className || ""}`}>
            {children}
          </div>
        );
      case "half":
        return (
          <div className={`govuk-grid-column-one-half ${className || ""}`}>
            {children}
          </div>
        );
      case "third":
        return (
          <div className={`govuk-grid-column-one-third ${className || ""}`}>
            {children}
          </div>
        );
      case "two-thirds":
        return (
          <div className={`govuk-grid-column-two-thirds ${className || ""}`}>
            {children}
          </div>
        );
      case "quarter":
        return (
          <div className={`govuk-grid-column-one-quarter ${className || ""}`}>
            {children}
          </div>
        );
      default:
        return (
          <div className={`govuk-grid-column-full ${className || ""}`}>
            {children}
          </div>
        );
    }
  }
  return renderGrid();
}
