import React from "react";
import InputProps from "./input";

interface CheckboxInputProps extends InputProps {
  small?: boolean;
  checked: boolean;
}
export default function CheckboxInput({
  name,
  className,
  defaultValue,
  disabled,
  error,
  errorClassName,
  helperClassName,
  helperText,
  label,
  labelClassName,
  value,
  type = "checkbox",
  onChange,
  checked,
  small = false
}: CheckboxInputProps) {
  return (
    <div
      className={`govuk-form-group ${error ? "govuk-form-group--error" : ""}`}
    >
      {helperText && (
        <span
          id={`${name}-hint"`}
          className={`govuk-hint ${helperClassName || ""}`}
        >
          {helperText}
        </span>
      )}
      {error && (
        <span
          id={`${name}-error`}
          className={`govuk-error-message ${errorClassName || ""}`}
        >
          <span className="govuk-visually-hidden">Error:</span> {error}
        </span>
      )}
      <div
        className={`${small ? "govuk-checkboxes--small" : "govuk-checkboxes"}`}
      >
        <div className="govuk-checkboxes__item">
          <input
            checked={checked}
            onChange={onChange}
            disabled={disabled}
            className={`govuk-checkboxes__input ${className || ""}`}
            id={name}
            name={name}
            type={type}
            value={value}
            defaultValue={defaultValue}
          />
          <label
            className={`govuk-label govuk-checkboxes__label ${
              labelClassName || ""
            }`}
            htmlFor={name}
          >
            {label}
          </label>
        </div>
      </div>
    </div>
  );
}
