import React from "react";

import HeaderLogo from "./HeaderLogo";
import HeaderNavigation from "./HeaderNavigation";
import HeaderUser from "./HeaderUser";

export interface HeaderProps {
  className?: string;
  fullWidth?: boolean;
  navigation?: Array<{ item: string; href: string; active: boolean }>;
  service?: string;
  title?: string;
  user?: string;
  withLogo?: boolean;
  onLogout?: () => void;
}

export default function Header({
  className,
  fullWidth = false,
  navigation,
  service,
  title,
  user,
  withLogo,
  onLogout
}: HeaderProps) {
  return (
    <header
      className={`idsk-header ${className || ""}`}
      role="banner"
      data-module="idsk-header"
    >
      <div
        className={`idsk-header__container ${
          fullWidth
            ? "govuk-header__container--full-width"
            : "govuk-width-container"
        } ${onLogout ? "govuk-header__container--with-user" : ""}`}
      >
        <HeaderLogo title={title} withLogo={withLogo} />
        <HeaderNavigation service={service} navigation={navigation} />
        {onLogout && <HeaderUser onLogout={onLogout} user={user} />}
      </div>
    </header>
  );
}
