import React from "react";

type TypographySizes = "xl" | "l" | "m" | "s";
type TypographyTypes = "h1" | "h2" | "h3" | "h4" | "p" | "a";

export interface TypographyProps {
  children: React.ReactNode;
  className?: string;
  href?: string;
  onClick?: () => void;
  type?: TypographyTypes;
  size?: TypographySizes;
}

export default function Typography({
  children,
  className,
  href = "#",
  onClick,
  type = "p",
  size = "m"
}: TypographyProps) {
  function renderText() {
    switch (type) {
      case "h1":
        return (
          <TypographyHeaderOne
            children={children}
            className={className}
            onClick={onClick}
            size={size}
          />
        );
      case "h2":
        return (
          <TypographyHeaderTwo
            children={children}
            className={className}
            onClick={onClick}
            size={size}
          />
        );
      case "h3":
        return (
          <TypographyHeaderThree
            children={children}
            className={className}
            onClick={onClick}
            size={size}
          />
        );
      case "h4":
        return (
          <TypographyHeaderFour
            children={children}
            className={className}
            onClick={onClick}
            size={size}
          />
        );
      case "a":
        return (
          <TypographyAnchor
            children={children}
            className={className}
            href={href}
            onClick={onClick}
            size={size}
          />
        );
      case "p":
        return (
          <TypographyParagraph
            children={children}
            className={className}
            onClick={onClick}
            size={size}
          />
        );
      default:
        return (
          <TypographyParagraph
            children={children}
            className={className}
            onClick={onClick}
            size={size}
          />
        );
    }
  }
  return renderText();
}

function TypographyHeaderOne({
  children,
  className,
  onClick,
  size
}: TypographyProps) {
  return (
    <h1
      onClick={onClick}
      style={onClick && { cursor: "pointer" }}
      className={`govuk-heading-${size} ${className || ""}`}
    >
      {children}
    </h1>
  );
}

function TypographyHeaderTwo({
  children,
  className,
  onClick,
  size
}: TypographyProps) {
  return (
    <h2
      onClick={onClick}
      style={onClick && { cursor: "pointer" }}
      className={`govuk-heading-${size} ${className || ""}`}
    >
      {children}
    </h2>
  );
}

function TypographyHeaderThree({
  children,
  className,
  onClick,
  size
}: TypographyProps) {
  return (
    <h3
      onClick={onClick}
      style={onClick && { cursor: "pointer" }}
      className={`govuk-heading-${size} ${className || ""}`}
    >
      {children}
    </h3>
  );
}

function TypographyHeaderFour({
  children,
  className,
  onClick,
  size
}: TypographyProps) {
  return (
    <h4
      onClick={onClick}
      style={onClick && { cursor: "pointer" }}
      className={`govuk-heading-${size} ${className || ""}`}
    >
      {children}
    </h4>
  );
}

function TypographyAnchor({
  children,
  className,
  href,
  onClick,
  size
}: TypographyProps) {
  return (
    <a
      onClick={onClick}
      style={onClick && { cursor: "pointer" }}
      className={`govuk-body-${size} govuk-link ${className || ""}`}
      href={href}
    >
      {children}
    </a>
  );
}

function TypographyParagraph({
  children,
  className,
  onClick,
  size
}: TypographyProps) {
  return (
    <p
      style={onClick && { cursor: "pointer" }}
      onClick={onClick}
      className={`govuk-body-${size} ${className || ""}`}
    >
      {children}
    </p>
  );
}
