import React from "react";
import InputProps from "./input";

interface SelectInputProps extends InputProps {
  options: Array<{ value: string | number; label: string }>;
}

export default function SelectInput({
  name,
  options,
  className,
  defaultValue,
  disabled,
  error,
  errorClassName,
  helperClassName,
  helperText,
  label,
  labelClassName,
  value,
  onChange
}: SelectInputProps) {
  return (
    <div
      className={`govuk-form-group ${error ? "govuk-form-group--error" : ""}`}
    >
      {label && (
        <label className={`govuk-label ${labelClassName || ""}`} htmlFor={name}>
          {label}
        </label>
      )}
      {helperText && (
        <span
          id={`${name}-hint"`}
          className={`govuk-hint ${helperClassName || ""}`}
        >
          {helperText}
        </span>
      )}
      {error && (
        <span
          id={`${name}-error`}
          className={`govuk-error-message ${errorClassName || ""}`}
        >
          <span className="govuk-visually-hidden">Error:</span> {error}
        </span>
      )}
      <select
        className={`govuk-select ${className || ""}`}
        id={name}
        disabled={disabled}
        name={name}
        defaultValue={defaultValue}
        value={value}
        onChange={onChange}
      >
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </div>
  );
}
