import React from "react";

import TableCell from "./TableCell";

interface TableColumnProps<DATA> {
  key: keyof DATA;
  label: string;
  render?: (values: DATA) => React.ReactNode;
  align?: "left" | "right";
}

interface TableProps<DATA> {
  columns: Array<TableColumnProps<DATA>>;
  data: DATA[];
  primaryKey: keyof DATA;
  title?: string;
  withHeader?: boolean;
}

export default function Table<DATA>({
  columns,
  data,
  primaryKey,
  title,
  withHeader = true
}: TableProps<DATA>) {
  return (
    <table className="govuk-table">
      {title && (
        <caption className="govuk-table__caption govuk-heading-m">
          {title}
        </caption>
      )}
      {withHeader && (
        <thead className="govuk-table__head">
          <tr className="govuk-table__row">
            {columns.map((col) => (
              <th
                key={`header-${col.key}`}
                scope="col"
                className={`govuk-table__header ${
                  col.align === "right"
                    ? "govuk-table__header govuk-table__header--numeric"
                    : ""
                }`}
              >
                {col.label}
              </th>
            ))}
          </tr>
        </thead>
      )}
      <tbody className="govuk-table__body">
        {data.length > 0 &&
          data.map((it, dataIndex) => (
            <tr key={`${primaryKey}-${dataIndex}`} className="govuk-table__row">
              {columns.map((col, colIndex) => {
                const isPrimary = it[primaryKey] === it[col.key];
                const valueToRender = col.render ? col.render(it) : it[col.key];

                return (
                  <TableCell
                    align={col.align}
                    key={`${colIndex}-${dataIndex}`}
                    isPrimary={isPrimary}
                    value={valueToRender}
                  />
                );
              })}
            </tr>
          ))}
      </tbody>
    </table>
  );
}
