import React from "react";

import LogoMIRI from "./LogoMIRI";
import LogoMZ from "./LogoMZ";

interface FooterProps {
  className?: string;
  logo?: "MIRI" | "MZ";
  labels?: string[];
}

export default function Footer({
  className,
  logo = "MIRI",
  labels
}: FooterProps) {
  function renderLogo() {
    switch (logo) {
      case "MIRI":
        return <LogoMIRI />;
      case "MZ":
        return <LogoMZ />;
      default:
        return <LogoMIRI />;
    }
  }
  return (
    <footer className={`idsk-footer ${className || ""}`} role="contentinfo">
      <div className="govuk-width-container">
        <div className="idsk-footer__meta">
          <div className="idsk-footer__meta-item">{renderLogo()}</div>
          <div className="idsk-footer__meta-item idsk-footer__meta-item--grow">
            {labels &&
              labels.map((label, index) => (
                <div key={`footer-label-${index}`}>
                  <span className="idsk-footer__licence-description">
                    {label}
                  </span>
                </div>
              ))}
            <h2 className="govuk-visually-hidden">Support links</h2>
          </div>
        </div>
      </div>
    </footer>
  );
}
